.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    right: 2rem;
    bottom: 3rem;
    transition:0.3s;
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(10px);

}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: white;
}

.header__socials::before {
    content: '';
    width: 1px;
    height: 2rem;
    background: white;
}

 a {
    background: transparent;
    padding: 0.1rem;
    border-radius: 100%;
    display: flex;


}

a:hover {
    background-color: rgba(0, 0, 0, 1);
    transition:0.3s;
    border-radius: 0.5rem;
}

@media screen and (max-width: 480px) {
    .header__socials {
        display: none;
    }
    
}