.nav__container {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    backdrop-filter: blur(15px);
    border-radius: 15px;
}

.nav__container a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: white;
    font-size: 3rem;
}

.nav__container a:hover {
    background-color: rgba(0, 0, 0, 0.4);
}