@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@200;300&family=League+Spartan&display=swap');

h3 {
    color: #fff;
    font-family: Kumbh Sans;
    font-size: 3vw;
}

h4 {
    color: #fff;
    font-family: Kumbh Sans;
    font-size: 2.5vw;
    padding-bottom: 240px;
}

hr {
    width: 20%;
}

.myButton {
	background-color:transparent;
	border-radius:28px;
	border:4px solid #ffffff;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Kumbh Sans;
	font-size:2.3vw;
	padding:1.5vw 2.3vw;
	text-decoration:none;

}
.myButton:hover {
	background-color:#9551b93a;
}
.myButton:active {
	position:relative;
	top:1px;
}

.padding {
    padding-top:1130px;
}


